<template>
  <div class="flex-fill">
    <div class="p-2">
      <button class="btn btn-success" type="button" @click="saveChanges">
         
      </button>

      <button class="btn btn-info ms-2" type="button" @click="$refs.editableFrame.requestFullscreen()">
          
      </button>
    </div>
    <iframe ref="editableFrame" @load="onFrameLoad"></iframe>
  </div>

  <div class="modal fade" id="editListModal" ref="editListModal" tabindex="-1" aria-labelledby="editListModalLabel"
       aria-hidden="true"
  >
    <div class="modal-dialog modal-dialog-centered modal-lg">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="editListModalLabel">  </h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">

          <div class="p-2">
            <button class="btn btn-primary"
                    @click="Array.isArray(selectedListForEdit.currentList) ? selectedListForEdit.currentList.push('') : selectedListForEdit.currentList = ['']"
            >
                
            </button>
          </div>

          <ul class="mt-3">
            <li v-for="(x,k) in selectedListForEdit.currentList" :key="k" class="d-flex p-1 align-items-center">
              <input type="text" class="form-control" v-model="selectedListForEdit.currentList[k]"/>
              <span class="ms-2 fa fa-trash text-danger cursor-pointer"
                    @click="selectedListForEdit.currentList.splice(k,1)"
              ></span>
            </li>
          </ul>

        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-primary" @click="saveEditingList"> </button>
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal"></button>
        </div>
      </div>
    </div>
  </div>

  <div class="modal fade" id="imageSelectorModal" ref="imageSelectorModal" tabindex="-1"
       aria-labelledby="imageSelectorModalLabel"
       aria-hidden="true"
  >
    <div class="modal-dialog modal-dialog-centered">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="imageSelectorModalLabel"> </h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">

          <h3>     </h3>
          <input type="file" class="form-control" ref="imageEditorInput" v-if="!cropper"
                 @change="onEditImageChanged"
                 accept="image/svg+xml,image/jpeg,image/png"
          />

          <div class="container-fluid overflow-hidden">
            <img ref="imageEditor"/>
          </div>

          <div class="mt-2">
            <label class="form-label"> </label>
            <textarea class="form-control" v-model="selectedImageAlternative"></textarea>
          </div>

        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-primary" @click="confirmImageEditor"> </button>
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal"></button>
        </div>
      </div>
    </div>
  </div>

  <div class="modal fade" id="SocialEditorModal" ref="SocialEditorModal" tabindex="-1"
       aria-labelledby="exampleModalLabel" aria-hidden="true"
  >
    <div class="modal-dialog modal-dialog-centered modal-lg">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">   </h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">

          <div>
            <button type="button" class="btn btn-primary" @click="selectSocials.push({})">
               
            </button>
          </div>
          <template v-if="selectSocials">
            <div class="mt-2 d-flex align-items-center" v-for="(x,k) in selectSocials" :key="k">
              <select class="form-select" style="max-width:150px" v-model="selectSocials[k].social">
                <option>telegram</option>
                <option>instagram</option>
                <option>twitter</option>
                <option>whatsapp</option>
              </select>
              <input type="url" class="form-control flex-fill" v-model="selectSocials[k].href">
              <button type="button" class="btn btn-danger" @click="selectSocials.splice(k,1)">
                
              </button>
            </div>
          </template>

        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-primary" @click="confirmSocialsEditor"> </button>
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal"></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Cropper from 'cropperjs'

export default {
  name: 'ThemeEditor',
  data: () => ({
    editListModal: undefined,
    editImageModal: undefined,
    SocialEditorModal: undefined,
    selectedListForEdit: {
      element: undefined,
      currentList: []
    },
    selectedImage: false,
    selectedImageAlternative: '',
    cropper: undefined,

    selectedSocialsItem: undefined,
    selectSocials: undefined
  }),
  methods: {
    onFrameLoad() {
      const document = this.$refs.editableFrame.contentWindow.document
      document.addEventListener('click', function (e) {
        if (e.target.tagName.toLowerCase() === 'a' || e.target.closest('a')) {
          e.preventDefault()
        }
      })
      document.querySelectorAll('[data-editable]')
          .forEach(x => {
            x.contentEditable = true
            x.oncontextmenu = cme => {
              cme.preventDefault()
            }
          })

      document.querySelectorAll('[data-editable-list]')
          .forEach(t => {
            const b = document.createElement('button')
            b.classList.add('btn', 'btn-primary', 'btn-sm')
            b.innerText = ' '
            b.type = 'button'
            b.addEventListener('click', () => this.onEditListClicked(t))
            b.setAttribute('data-editor-element', 'editor-element')
            t.parentNode.insertBefore(b, t)
          })

      document.querySelectorAll('[data-editable-image]')
          .forEach(img => {
            const loadEditor = () => {
              if (this.cropper) {
                this.cropper.destroy()
                this.cropper = undefined
              }
              this.$refs.imageEditor.src = ''
              this.selectedImageAlternative = img.alt
              this.selectedImage = img
              this.getEditImageModal()
                  .show()
            }

            const b = document.createElement('button')
            b.innerText = ' '
            b.style.width = 'auto'
            b.style.height = '25px'
            b.style.bottom = 'unset'
            b.setAttribute('data-editor-element', 'editor-element')
            b.addEventListener('click', loadEditor)
            img.parentNode.append(b)

            img.parentNode.addEventListener('contextmenu', e => {
              e.preventDefault()
              loadEditor()
            })
          })

      document.querySelectorAll('[data-editable-socials]')
          .forEach(socialContainer => {
            socialContainer.addEventListener('contextmenu', ev => {
              ev.preventDefault()
              this.selectedSocialsItem = socialContainer
              this.selectSocials = [...socialContainer.querySelectorAll(':scope > *:not(template)')].map(element => {
                return {
                  href: element.querySelector('a').href,
                  social: element.getAttribute('data-social')
                }
              })

              this.getSocialEditorModal()
                  .show()
            })
          })
    },
    saveChanges() {
      const s = this.$refs.editableFrame.contentWindow.document.documentElement.cloneNode(true)
      s.querySelectorAll('[contentEditable]')
          .forEach(x => x.contentEditable = false)
      s.querySelectorAll('[data-editor-element]')
          .forEach(x => x.remove())

      const win = window.open()
      win.document.write(s.outerHTML)
      win.document.close()
    },
    onEditListClicked(element) {
      this.selectedListForEdit = {
        element,
        currentList: [...element.querySelectorAll('li,ol')].map(x => x.innerText)
      }
      this.getEditListModal()
          .show()
    },
    getEditListModal() {
      if (!this.editListModal) {
        this.editListModal = new window.bootstrap.Modal(this.$refs.editListModal)
      }
      return this.editListModal
    },
    getEditImageModal() {
      if (!this.editImageModal) {
        this.editImageModal = new window.bootstrap.Modal(this.$refs.imageSelectorModal)
      }
      return this.editImageModal
    },
    getSocialEditorModal() {
      if (!this.SocialEditorModal) {
        this.SocialEditorModal = new window.bootstrap.Modal(this.$refs.SocialEditorModal)
      }
      return this.SocialEditorModal
    },
    saveEditingList() {
      if (this.selectedListForEdit && this.selectedListForEdit.element) {
        this.selectedListForEdit.element.querySelectorAll('li,ol')
            .forEach(x => {
              this.selectedListForEdit.element.removeChild(x)
            })

        const template = this.selectedListForEdit.element.querySelector('template')
        this.selectedListForEdit.currentList.forEach(x => {
          const node = template.content.cloneNode(true)
          const place = node.querySelector('[data-content]')
          place.parentNode.insertBefore(document.createTextNode(x), place)
          place.remove()

          this.selectedListForEdit.element.appendChild(node)
        })
      }
      this.getEditListModal()
          .hide()
    },
    onEditImageChanged() {
      if (this.$refs.imageEditorInput.files.length > 0 && this.selectedImage) {
        const fd = new FileReader()
        fd.onload = () => {
          this.$refs.imageEditor.src = fd.result
          this.cropper = new Cropper(this.$refs.imageEditor, { aspectRatio: this.selectedImage.dataset.aspect })
          this.$refs.imageEditorInput.value = ''
        }
        fd.readAsDataURL(this.$refs.imageEditorInput.files[0])
      }
    },
    confirmImageEditor() {
      if (this.selectedImage) {

        this.selectedImage.alt = this.selectedImageAlternative
        this.selectedImage.src = this.cropper.getCroppedCanvas()
            .toDataURL()

        this.getEditImageModal()
            .hide()
        this.cropper.destroy()
        this.cropper = undefined
        this.selectedImage = undefined
      }
    },
    confirmSocialsEditor() {
      if (this.selectedSocialsItem) {
        this.selectedSocialsItem.querySelectorAll(':scope > *:not(template)')
            .forEach(x => x.remove())
        const template = this.selectedSocialsItem.querySelector('template')
        this.selectSocials.forEach(x => {
          const item = template.content.cloneNode(true)

          item.children[0].outerHTML = item.children[0].outerHTML.replaceAll('[social]', x.social)
          item.querySelector('a').href = x.href
          this.selectedSocialsItem.appendChild(item)
        })

        this.getSocialEditorModal()
            .hide()
        this.selectedSocialsItem = this.selectSocials = undefined
      }
    }
  },
  mounted() {
    this.$fetch(this.$filters.serverUrl('template/index.html'))
        .then(x => {
          this.$refs.editableFrame.src = URL.createObjectURL(new Blob([x.data], { type: 'text/html' }))
        })
  }
}
</script>

<style lang="scss" scoped>
@import "~cropperjs/dist/cropper.min.css";

iframe {
  width: 100%;
  height: 100%;
}
</style>
